#root, .root {
  width: 100%;
  height: 100vh;
  background: #222;
  color: #FFF;
}

.root {
  display: flex;
  flex-direction: row;
  width: 100vw;
  height: 100vh;
}

.column-left {
  flex: 2;
  border-right: 1px solid #444;
}

.column-right {
  flex: 5;
}

.root > .column {
  padding: 5px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}

.handset-group button.ms-Nav-link {
  height: auto;
}

.line-group .ms-List-cell {
}

.grpHead {
  display: block;
  margin: 10px;
}

.nav-link-inner-flex {
  display: flex;
  width: 100%;
  align-items: center;
}

.nav-link-inner-flex .ms-Button--icon {
  height: 50px;
  width: 50px;
  margin: 0;
  padding: 0;
}

.nav-link-inner-flex .ms-Button--icon i {
  font-size: 34px !important;
}

.nav-link-inner-flex-text {
  flex: 1;
  text-align: left;
}

.line-group .nav-link-inner-flex-text {
  padding: 20px 0px 20px 20px;
}

.line-group .nav-link-inner-flex.active .nav-link-inner-flex-text {
  cursor: pointer;
}

.ms-List-cell {
  border-bottom: 1px solid #555;
  display: flex;
}

.line-group .icon-container {
  padding: 20px 0px 0px 20px;
  margin-right: -20px;
}

.on-air-icon {
  font-size: 18pt;
  padding-right: 10px;
}

.caller-unscreened {
  color: #C00;
}

.left-controls {
  display: flex;
  margin-top: -20px;
  padding-left: 25px;
  padding-right: 15px;
  flex-direction: column;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}

.line-icon {
  display: inline-block;
  width: 38px;
  border-radius: 300px;
  text-align: center;
  font-weight: 600;
  line-height: 38px;
  margin-right: 15px;
}

.setup-diag .ms-Dialog-main {
  max-width: 500px;
  width: 500px;
}